<template>
  <div class="site">
    <el-table :data="filelistData" style="width: 90%;margin: 20px;">
      <el-table-column label="名称">
        <template slot-scope="scope2">
          <i class="el-icon-folder" v-if="scope2.row.type === 'dir'"></i>
          <i class="el-icon-files" v-else></i>
          <span
            style="margin-left: 10px"
            @click="gogetFile(scope2.row.name, scope2.row.type)"
          >{{ scope2.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" prop="ctime"></el-table-column>
      <el-table-column label="修改时间" prop="utime"></el-table-column>

      <el-table-column align="right">
        <template slot="header" slot-scope>
          <el-button size="mini" icon="el-icon-back" @click="backDir()"></el-button>
          <el-button size="mini" icon="el-icon-right" @click="newDir()"></el-button>
          <el-button size="mini" icon="el-icon-plus" @click="newFile()"></el-button>
        </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            icon="el-icon-edit"
            @click="handleEdit(scope.row.name, scope.row.type)"
          ></el-button>
          <el-button
            type="danger"
            size="mini"
            icon="el-icon-delete"
            @click="handleDelete(scope.row.name, scope.row.type)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="编辑文件" :visible.sync="dialogFormVisible">
      <el-form :model="fileform">
        <el-form-item label="文件内容" :label-width="formLabelWidth">
          <el-input type="textarea" :rows="15" v-model="fileform.content"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">返 回</el-button>
        <el-button type="primary" @click="SaveFile">保 存</el-button>
      </div>
    </el-dialog>

    <el-dialog title="新建文件" :visible.sync="dialogFormVisible2">
      <el-form :model="fileform2">
        <el-form-item label="文件名称" :label-width="formLabelWidth">
          <el-input v-model="fileform2.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="文件内容" :label-width="formLabelWidth">
          <el-input type="textarea" :rows="15" v-model="fileform2.content"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false">返 回</el-button>
        <el-button type="primary" @click="SaveFile2">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "File",
  data() {
    return {
      basedir: "",
      filelistData: [],
      search: "",
      newdir: "",
      page: "",
      dialogFormVisible: false,
      dialogFormVisible2: false,
      fileform: {
        content: ""
      },
      fileform2: {},
      formLabelWidth: "120px"
    };
  },
  methods: {
    newDir() {
      localStorage.setItem("olddir", this.newdir);
      this.newdir = localStorage.getItem("newdir2");
      this.ongetFile();
    },
    backDir() {
      localStorage.setItem("newdir2", this.newdir);
      this.newdir = localStorage.getItem("olddir");
      this.ongetFile();
    },
    newFile() {
      this.dialogFormVisible2 = true;
      this.fileform2.basedir = this.basedir;
    },
    SaveFile2() {
      this.dialogFormVisible2 = false;
      var formData = new FormData();
      var file = `${this.fileform2.basedir}/${this.fileform2.name}`;
      formData.append("file", file);
      formData.append("content", this.fileform2.content);
      this.$http.upfile(formData).then(res => {
        if (res.errcode === 0) {
          this.$message({
            message: "新建文件成功",
            type: "success"
          });
          this.ongetFile();
        }
      });
    },
    SaveFile() {
      this.dialogFormVisible = false;
      var formData = new FormData();
      formData.append("file", this.fileform.filename);
      formData.append("content", this.fileform.content);
      this.$http.upfile(formData).then(res => {
        if (res.errcode === 0) {
          this.$message({
            message: "文件修改成功",
            type: "success"
          });
          this.ongetFile();
        }
      });
    },
    handleEdit(name, type) {
      if (type === "file") {
        this.dialogFormVisible = true;
        var file = `${this.basedir}/${name}`;
        this.fileform.filename = file;
        var params = {
          file: file
        };
        this.$http
          .getfile(params)
          .then(res => {
            this.errmsg = res.errmsg;
            if (res.errcode === 0) {
              this.fileform.content = res.data;
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    handleDelete(name, type) {
      var file = `${this.basedir}/${name}`;
      var params = {
        file: file,
        type: type
      };
      this.$http
        .delfile(params)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            this.$message({
              message: "文件删除成功",
              type: "success"
            });
            this.ongetFile();
          }
        })
        .catch(error => {});
    },
    gogetFile(name, type) {
      if (type === "dir") {
        localStorage.setItem("olddir", this.newdir);
        this.newdir = `${this.basedir}/${name}`;
        this.ongetFile();
      } else {
        this.handleEdit(name, type);
      }
    },
    handleCurrentChange(page) {
      this.page = page;
      this.ongetFtp();
    },
    ongetFile() {
      let params = {
        // d: localStorage.getItem('newdir'),
        d: this.newdir,
        page: this.page
      };
      this.$http
        .getfile(params)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            this.basedir = res.basedir;
            this.filelistData = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.newdir = this.$route.params.d;
    // localStorage.setItem("newdir", this.$route.params.d);
    this.ongetFile();
  }
};
</script>

<style>
</style>


